@use 'mq';
@use 'variables' as var;

// 로딩
.wrap_loading {
  @keyframes spinner {
    to {
      transform: rotate(1turn);
    }
  }

  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  height: 100dvh;
  background: rgba(255, 255, 255, 0.3);

  .loading {
    width: 50px;
    padding: 8px;
    aspect-ratio: 1;
    border-radius: 50%;
    background: var.$c-key;
    --_m: conic-gradient(#0000 10%, #000), linear-gradient(#000 0 0) content-box;
    -webkit-mask: var(--_m);
    mask: var(--_m);
    -webkit-mask-composite: source-out;
    mask-composite: subtract;
    animation: spinner 1s infinite linear;
  }
}

#wrap,
body,
html {
  height: 100%;
  font-family: var.$t-basic;
}
#wrap,
#header,
#container,
#footer {
  position: relative;
  width: 100%;
}
#wrap {
  overflow: inherit;
}

/* Header */
$header-h: 60px;
#header {
  position: fixed;
  top: 0;
  z-index: 100;
  height: $header-h;
  padding: 0 35px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.3);
  background: rgba(0, 0, 0, 0.6);

  @include mq.respond-to('small') {
    padding: 0 10px;
  }
  .inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
  }
  h1 {
    padding-top: 3px;
    white-space: nowrap;

    a {
      letter-spacing: 0.1em;

      @include mq.respond-to('small') {
        font-size: 28px;
      }
      @include mq.respond-to('medium') {
        font-size: 34px;
      }
    }
  }
  #nav {
    height: 100%;

    &.on {
      @include mq.respond-to('small') {
        display: table;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.9);
      }
      @include mq.respond-to('medium') {
        display: table;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.9);
      }
      @include mq.respond-to('large') {
        display: block;
        position: relative;
        width: auto;
        height: auto;
        background: transparent;
      }

      .burgur {
        .bar1 {
          transform: rotate(45deg);
          margin-top: 11px;
        }
        .bar2 {
          background: rgba(255, 255, 255, 0);
        }

        .bar3 {
          transform: rotate(-45deg);
          margin-top: -16px;
        }
      }

      .gnb {
        @include mq.respond-to('small') {
          display: table-cell;
        }
        @include mq.respond-to('medium') {
          display: table-cell;
        }
        @include mq.respond-to('large') {
          display: block;
        }
      }
    }
  }
  a {
    color: #fff;
  }

  $burgur-w: 45px;
  .burgur {
    cursor: pointer;
    position: absolute;
    right: 25px;
    width: $burgur-w;
    margin: 10px 0;
    padding: 10px;

    @include mq.respond-to('small') {
      display: block;
      right: 10px;
    }
    @include mq.respond-to('medium') {
      display: block;
    }
    @include mq.respond-to('large') {
      display: none;
    }

    span {
      display: block;
      width: 100%;
      height: 3px;
      background: rgba(255, 255, 255, 1);
      -o-transition: all 0.1s ease-in;
      -ms-transition: all 0.1s ease-in;
      -moz-transition: all 0.1s ease-in;
      -webkit-transition: all 0.1s ease-in;
      transition: all 0.1s ease-in;

      &.bar1,
      &.bar3 {
        margin-top: 0;
      }
      &.bar2 {
        margin: 5px 0;
      }
    }
  }

  .gnb {
    @include mq.respond-to('small') {
      float: none;
      display: none;
      vertical-align: middle;
    }
    @include mq.respond-to('medium') {
      float: none;
      display: none;
      vertical-align: middle;
      height: 100%;
    }
    @include mq.respond-to('large') {
      display: block;
      float: right;
    }

    > li {
      cursor: pointer;
      position: relative;
      height: $header-h;
      text-align: center;

      @include mq.respond-to('small') {
        display: block;
        margin: 0;
      }
      @include mq.respond-to('medium') {
        display: block;
        margin: 0;
      }
      @include mq.respond-to('large') {
        display: inline-flex;
        align-items: flex-start;
        justify-content: center;
        margin-left: 45px;
      }
      > a {
        outline: 0;
        position: relative;
        height: 100%;
        padding: 22px 0 20px;
        font-size: 18px;
        font-weight: bold;
        letter-spacing: 0.5px;

        @include mq.respond-to('small') {
          display: inline-block;
        }
        @include mq.respond-to('medium') {
          display: inline-block;
        }
        @include mq.respond-to('large') {
          display: block;
        }
        &::after {
          content: '';
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          height: 3px;
          width: 0;
          background: #fff;
        }
      }

      &:hover,
      &.on {
        > a {
          &::after {
            width: 100%;
          }
        }
      }
    }
    .sub_menu {
      display: none;
      padding: 7px 0;

      @include mq.respond-to('small') {
        position: static;
        background: transparent;
      }
      @include mq.respond-to('medium') {
        position: static;
        background: transparent;
      }
      @include mq.respond-to('large') {
        position: absolute;
        top: 60px;
        background: var.$c-dim;
      }

      &.show {
        display: block;
      }
      > li {
        opacity: 0;
        color: rgba(255, 255, 255, 0.6);
        white-space: nowrap;
        text-align: center;

        &:hover,
        &.on {
          > a {
            color: rgba(255, 255, 255, 1);
          }
        }
        a {
          display: block;
          padding: 6px 25px;
          color: rgba(255, 255, 255, 0.6);
          white-space: nowrap;
          font-weight: 600;

          &::before {
            @include mq.respond-to('small') {
              content: '- ';
            }
            @include mq.respond-to('medium') {
              content: '- ';
            }
            @include mq.respond-to('large') {
              content: '';
            }
          }
          &::after {
            @include mq.respond-to('small') {
              content: ' -';
            }
            @include mq.respond-to('medium') {
              content: ' -';
            }
            @include mq.respond-to('large') {
              content: '';
            }
          }
        }
      }
    }
  }
  &.main {
    box-shadow: none;
    background: transparent;

    .gnb {
      .sub_menu {
        background: transparent;
      }
    }
  }
}

.inner {
  margin: 0 auto;

  @include mq.respond-to('small') {
    width: 100%;
  }
  @include mq.respond-to('medium') {
    width: 100%;
  }
  @include mq.respond-to('large') {
    width: mq.$lg-w;
  }
  @include mq.respond-to('wide') {
    width: mq.$w-w;
  }
}

#container {
  section:first-child {
    padding-top: $header-h;
  }
  section {
    position: relative;
    min-height: 100vh;
  }

  .inner {
    text-align: center;

    @include mq.respond-to('small') {
      padding: 30px 5px;
    }
    @include mq.respond-to('medium') {
      padding: 30px 10px;
    }
    @include mq.respond-to('large') {
      padding: 40px 0;
    }
    @include mq.respond-to('wide') {
      padding: 50px 0;
    }

    .title {
      opacity: 0;
      display: inline-block;
      margin: 30px 0;
      padding: 0 3px;
      border-bottom: 6px solid #85b6a5;

      @include mq.respond-to('small') {
        height: 29px;
        font-size: 30px;
      }
      @include mq.respond-to('medium') {
        height: 40px;
        font-size: 42px;
      }
    }
    .title_desc {
      padding: 0 10px 10px 10px;
    }
  }

  /* Footer*/
  #Footer {
    padding: 30px 0;
    background: #839c99;
    color: #fff;

    .inner {
      padding: 0;

      p {
        font-size: 13px;
        text-align: center;
      }

      .last_comment {
        margin-bottom: 10px;
        font-size: 20px;
        font-weight: bold;
        letter-spacing: 1.2px;
      }
    }
  }
}

.section {
  position: relative;

  .content {
    @include mq.respond-to('small') {
      margin: 15px auto 0;
    }
    @include mq.respond-to('medium') {
      margin: 20px auto 0;
    }
    @include mq.respond-to('large') {
      margin: 35px auto 0;
    }
  }
}
