@use 'mixins';
@use 'mq';
@use 'variables';

/* Common */
// next.js 이미지 전용 리셋
.responsive_size {
  object-fit: contain !important;
  position: relative !important;
  height: auto !important;
  width: auto !important;
}

// 스크롤 맨위로 가기
.btn_top {
  display: block;
  cursor: pointer;
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 100;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  background: variables.$c-dim url('../../public/images/btn_top.png') no-repeat center 12px;
}
.btn_top.zero {
  display: none;
}

// 리스트 기본 레이아웃
.list_wrap {
  > li {
    @include mq.respond-to('small') {
      width: 100%;
      margin: 5px 0;
    }
    @include mq.respond-to('medium') {
      width: 50%;
      margin: 10px 0;
    }
    @include mq.respond-to('large') {
      width: 33.33%;
      margin: 15px 0;
    }
    @include mq.respond-to('wide') {
      width: 25%;
      margin: 25px 0;
    }
  }
}

// 코멘트
.comment {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
  padding: 10px;
  border: 1px solid #d9d9d9;
  box-shadow: 0 0 5px rgba(62, 105, 132, 0.4);
  background: #fff;
  font-size: 14px;

  &.sample {
    width: 280px;
    color: variables.$c-sub;
  }
  &.comming {
    width: 150px;
    color: variables.$c-pre;
  }
  &.close {
    width: 280px;
    color: variables.$c-warn;
  }
}

// 링크 없음
.nolink {
  display: inline-block;
  font-size: 14px;
  color: #999;
}
// 링크
.link {
  color: variables.$c-pre;

  &:hover {
    text-decoration: underline;
  }
}

// 입력
input[type='text'] {
  outline: 0;
  height: 30px;
  padding: 0 10px;
  border: 0;
  border-bottom: 2px solid variables.$c-key;
  background: rgba(255, 255, 255, 0.4);
  line-height: 1;
  transition: all 0.6s;

  &:focus {
    background: rgba(255, 255, 255, 1);
  }
}

// 버튼
.btn {
  &.basic {
    @include mixins.btn('basic');
  }
  &.second {
    @include mixins.btn('second');
  }
  &.warn {
    @include mixins.btn('warn');
  }
  &.md {
    width: auto;
    height: 41px;
    margin: 0 3px;
    padding: 0 20px;
    border-radius: 21px;
    font-size: 20px;
    line-height: 40px;
  }
}

// 알약 스타일(노버튼)
.pill_group {
  display: flex;
  font-size: 14px;
  text-align: center;
  text-decoration: none;
  line-height: 100%;

  > * {
    margin: 0 3px;
    padding: 6px 12px;
    border: 1px solid variables.$c-btn-line;
    border-radius: 14px;
    background: variables.$c-dim;
    color: variables.$c-btn-line;
  }
}

// 팝업 - 컨텐츠형
.modal_dim {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 100;
  background: variables.$c-dim;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 20px;

  .modal_layer {
    max-width: 100%;
    max-height: 100%;
    background: #fff;

    @include mq.respond-to('small') {
      width: 100%;
    }
    @include mq.respond-to('medium') {
      width: 100%;
    }
    @include mq.respond-to('large') {
      width: 720px;
    }
  }
  .modal_header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 40px;
    padding: 0 10px;
    background: #d5e4df url('../../public/images/pattern_bg2.png');

    .modal_title {
      padding-left: 10px;
      line-height: 1;
    }
    .modal_close {
      width: 20px;
      height: 20px;
      border: 0;
      background: transparent;
      text-align: center;
      color: variables.$c-sub;
      font-weight: 700;
      line-height: 1;
    }
  }
  .modal_content {
    padding: 20px;
  }
}

// 게시판 - 데이터 없음
.no_data {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50vh;
}
